@import url(https://fonts.googleapis.com/css?family=Dawning+of+a+New+Day|Josefin+Slab&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
   font-family: 'Josefin Slab', serif;
   background-color: #f7f5f6;
   height: 100%;
   position: relative;
}

header {
  background-color: black;
  position: fixed;
  width: 100vw;
  border-bottom: 1px solid black;
  padding-bottom: 10px;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: space-between;
  min-height: 80px;
  box-shadow: 2px 0 10px 5px black;
  z-index: 1;
}

h1 {
  margin: 0 0 0 15px;
  font-family: 'Dawning of a New Day', cursive;
  color: #d6caeb;
  font-size: 52px;
  text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 20px #ff0080, 0 0 30px #ff0080, 0 0 40px #ff0080, 0 0 55px #ff0080, 0 0 75px #ff0080;
}

nav {
  margin: 0 50px 0 0;
}

nav a {
  text-decoration: none;
  color: #d6caeb;
  margin: 0 5%;
  font-size: 36px;
  -webkit-transition: color .5s ease-in-out;
  transition: color .5s ease-in-out;
}

nav a:hover {
  color: #ff0080;
}

span {
  font-size: 62px;
}

.component {
  padding-top: 150px;
  width: 90%;
  margin: 0 auto 80px auto;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}

* {
 box-sizing: border-box;
}

*:before,
*:after {
 box-sizing: border-box;
}

.App {
 min-height: 100vh; /* will cover the 100% of viewport */
 overflow: hidden;
 display: block;
 position: relative;
 padding-bottom: 100px; /* height of your footer */
}

footer {
 position: absolute;
 bottom: 0;
 width: 100%;
}

.artImg {
  width: 30%;
  height: 25vw;
  margin: 2vw 1vw;
}

img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  -webkit-transition: -webkit-filter 1s ease-in-out;
  transition: -webkit-filter 1s ease-in-out;
  transition: filter 1s ease-in-out;
  transition: filter 1s ease-in-out, -webkit-filter 1s ease-in-out;
  -webkit-filter: grayscale(100%); /* Ch 23+, Saf 6.0+, BB 10.0+ */
  filter: grayscale(100%); /* FF 35+ */
}

img:hover {
  -webkit-filter: grayscale(0%); /* Ch 23+, Saf 6.0+, BB 10.0+ */
  filter: grayscale(0%); /* FF 35+ */
}

#myImg {
  border-radius: 5px;
  cursor: pointer;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

#myImg:hover {opacity: 0.7;}

.modal-content {
  display: flex;
  flex-direction: row;
}

/* The Modal (background) */
.modal {
  display: block; /* Hidden by default */
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  padding-top: 100px; /* Location of the box */
  padding-bottom: 40px;
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0,0,0,0.9); /* Black w/ opacity */
}

/* Modal Content (image) */
.modal-img {
  margin: auto;
  display: block;
  width: auto;
  height: 80vh;
  -webkit-filter: grayscale(0%); /* Ch 23+, Saf 6.0+, BB 10.0+ */
  filter: grayscale(0%); /* FF 35+ */
}

/* Caption of Modal Image */
#caption {
  margin: auto;
  display: block;
  width: 35%;
  text-align: center;
  color: #ccc;
  padding: 10px 0;
  height: auto;
}

/* Add Animation */
.modal-content, #caption {
  -webkit-animation-name: zoom;
  -webkit-animation-duration: 0.6s;
  animation-name: zoom;
  animation-duration: 0.6s;
}

@-webkit-keyframes zoom {
  from {-webkit-transform: scale(0)}
  to {-webkit-transform: scale(1)}
}

@keyframes zoom {
  from {-webkit-transform: scale(0.1);transform: scale(0.1)}
  to {-webkit-transform: scale(1);transform: scale(1)}
}

/* The Close Button */
.close {
  position: absolute;
  top: 15px;
  right: 35px;
  color: #f1f1f1;
  font-size: 40px;
  font-weight: bold;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.close:hover,
.close:focus {
  color: #bbb;
  text-decoration: none;
  cursor: pointer;
}

.articleLink {
  margin: 0 0;
  padding: 0 0;
}

a {
  text-decoration: none;
  color: #ff0080;
  -webkit-transition: color .5s ease-in-out;
  transition: color .5s ease-in-out;
}

a:hover {
  color: #d6caeb;
}

h2 {
  font-size: 36px;
}

p {
  font-size: 24px;
}

article {
  width: 60%;
  margin: 0 auto;
}

